.image.osc {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    overflow: hidden;
    position: relative;
    z-index: 1;
}
@media (min-width: $(desktop)) {
    .image.osc {
        width: 200px;
        height: 200px;
    }
}
.image.osc figure {
    width: 100%;
    height: 100%;
}
.image.osc img {
    height: 100%;
    width: 100%;
    -o-object-fit: cover;
       object-fit: cover;
    -o-object-position: center center;
       object-position: center center;
}
@media (min-width: $(desktop)) {
    .image.osc img {
        -o-object-position: center center;
           object-position: center center;
    }
}
